






































import { Prop, Vue, Component, Emit } from "vue-property-decorator";
import { clone } from "lodash";

@Component({})
export default class MovieModel extends Vue {
  @Prop({ required: true })
  private value!: any;

  @Prop({ required: true })
  private mediaURL!: any;

  @Prop({ required: true })
  private isVideo!: any;

  private cloneValue = clone(this.value);

  @Emit("input")
  close() {
    return false;
  }
}
